.main-bg {
    @apply bg-top bg-no-repeat bg-cover;
    width: 100%;
    height: 100%;
}
.app-wrapper {
    background-color: rgba(0, 0, 0, 0.4);
    width: 100%;
    height: 100%;
}
.app {
    @apply overflow-auto flex flex-col;
    width: 100%;
    height: 100%;
}

.moreless-button:hover .arrow-up::after {
    @apply rounded-full;
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgba(255, 255, 255, 0.5)
}

.arrow-up {
    @apply rounded-full;
    position: relative;
    width: 40px;
    height: 40px;
}

.flip-arrow {
    transform: scaleY(-1);
}

.button-text {
    @apply font-bold mr-2;

    color: var(--gray-button-text-color);
    width: 55px;
    letter-spacing: 0.2em;
}

@media screen and (min-width: 768px) {
    .button-text {
        width: 80px;
    }
    
}
@media screen and (min-width: 1024px) {
    .main-bg{
        background-size: cover 110%;
    }
}

@media screen and (min-width: 1300px) {
    .top {
        @apply my-16 mx-auto;
        width: 1200px;
    }
}